import React from 'react'
import Link from 'gatsby-link'

import Chilly from '../components/Chilly'

class Template extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      chillyActive: false
    }
    this.closeChilly = this.closeChilly.bind(this)
    this.openChilly = this.openChilly.bind(this)
    this.links = this.formatLinks(props.data)
  }

  closeChilly() {
    console.log("Closing Chilly")
    this.setState({
      chillyActive: false
    })
  }

  openChilly() {
    console.log("Opening Chilly")
    this.setState({
      chillyActive: true
    })
  }

  formatLinks(data) {
    console.log(data)
    return data.allJavascriptFrontmatter.edges.map(i => {
      return i.node
    })
  }

  render() {
    const { location, children } = this.props
    return (
      <div>
        <div onClick={this.closeChilly}>
          {children()}
        </div>
        <Chilly
          onClick={this.openChilly}
          open={this.state.chillyActive}
          links={this.links}
        />
      </div>
    )
  }
}

Template.propTypes = {
  children: React.PropTypes.func,
  location: React.PropTypes.object,
  route: React.PropTypes.object,
}

export default Template

export const pageQuery = graphql`
  query InChillyQuery {
    allJavascriptFrontmatter(filter: {
      frontmatter: {
        inChilly: {
          eq: true
        }
      }
    }) {
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
          }
        }
      }
    }
  }
`;



